import './App.css';

import { CardList } from './components/card-list/card-list.component';
import { Component } from 'react';

class App extends Component{

  constructor(props)
  {
    super(props);

      this.state ={
        monsters :[],
        searchfield : ""
      }

  }

componentDidMount()
{
  fetch("https://jsonplaceholder.typicode.com/users")
  .then(response => response.json())
  .then(users => this.setState({monsters :users}))
}



  render()
  {
    const {monsters,searchfield} = this.state;
    const filtersearch = monsters.filter(monster => monster.username.toLowerCase().includes(searchfield.toLowerCase()) )
    return (
      <div className="App">

        <input className="search" placeholder="search"  onChange={e => this.setState({searchfield : e.target.value})}></input>

         <CardList monsters ={filtersearch}></CardList>
         
      </div>

    )
  }
}

export default App;
