import React from 'react';
import "./card.styles.css";

export const Card = (props) => (
  <div className="card-container">
    <img src={`https://robohash.org/E${props.monster.id}V.png?set=set4&size=180x180`} alt="cat"></img>
       <h1 className="font-all">{props.monster.name}</h1>
       <h2 className="font-all">{props.monster.username}</h2>

   </div>
)